import { useState } from 'react';
import React from 'react';

// MUI Includes
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Container from '@mui/material/Container';
import CssBaseline from '@mui/material/CssBaseline';

// MUI Icon Includes
import EmojiObjectsIcon from '@mui/icons-material/EmojiObjects';
import MusicNoteIcon from '@mui/icons-material/MusicNote';

// Local Includes
import EffectsList from './EffectsList';
import Header from './Header';
import SettingsDialog from './SettingsDialog';





class Controller extends React.Component {
  state = {
    connected: false,
    settingsOpen: false,
    patterns: [],
    animations: [],
    brightness: 0,
    volume: 0
  }

  batTreeDevice = undefined;
  batTreeCharacteristic = undefined;
  BatState = 'connect';
  numPatterns = 0;
  currentPattern = 0;
  numAnimations = 0;
  currentAnimation = 0;
  lastPlayedPattern = 0;
  

  handleOpenSettings = () => {
    this.setState(() => ({settingsOpen: true}))
  };

  handleCloseSettings = () => {
    this.setState(() => ({settingsOpen: false}))
  };

  
  onClickConnect = () => {
    // Try to find the BAT Tree device
    let filters = [];
    filters.push({name: "BAT Tree"});
    let optionalServices = ['0000ffe0-0000-1000-8000-00805f9b34fb'];
    console.log(optionalServices);
    let options = {};
    options.filters = filters;
    options.optionalServices = optionalServices;
    console.log('Requesting Bluetooth Device...');
    console.log('with ' + JSON.stringify(options));

    // Request the device with the options
    navigator.bluetooth.requestDevice(options)

    // If success, connect to GATT server
    .then(device => {
      console.log('Connecting to GATT Server...');
      console.log(device);
      this.batTreeDevice = device;
      return this.batTreeDevice.gatt.connect();
    })

    // Grab the primary service for the BLE controller
    .then(server => {
      console.log('Getting Primary Service...');
      return server.getPrimaryService(0xFFE0);
    })

    // Connect to the characteristic
    .then(service => {
      console.log('Getting Custom Characteristic...');
      return service.getCharacteristic(0xFFE1);
    })

    // Register callback when connected to characteristic
    .then(characteristic => {
      console.log('We got all the way to the end!');
      this.batTreeCharacteristic = characteristic;
      this.batTreeCharacteristic.addEventListener('characteristicvaluechanged', this.handleMessageReceived);
      this.batTreeCharacteristic.startNotifications();

      //  Update the state so connect goes away
      this.setState(() => ({connected: true}));
    })
    .then(() => {
      this.BatState = 'queryNumPat';
      this.batTreeCharacteristic.writeValueWithoutResponse(new TextEncoder().encode('NumPat\n'));
    })
    .catch(error => {
      console.log('WRONG! ' + error);
    });

  }



  
  handleMessageReceived = (event) => {
    let respStr = String.fromCharCode.apply(null, new Uint8Array(event.target.value.buffer));
    if (respStr === "EOE") {
      console.log("Let us ignore the EOE, shall we?");
      // When we receive EOE, we know the current effect has finished playing.
      // If it was a pattern, it will repeat.
      // If it was an animation, it will resume the last pattern
      // Therefore, we can set the active boolean of the last played pattern to true
      // First, clear all selected icons
      let patterns = this.state.patterns;
      let animations = this.state.animations;
      patterns.forEach((pattern) => { pattern.active = false; })
      animations.forEach((animation) => {animation.active = false; })

      // Enable the selected icon
      patterns[this.lastPlayedPattern].active = true;
      
      this.setState(() => ({ patterns, animations }));
      return;
    }

    if (this.BatState === 'queryNumPat') {
      this.numPatterns = parseInt(respStr);
      console.log('The number of patterns is ' + this.numPatterns);

      // Now that we have the number of patterns, let's get them
      if (this.numPatterns === 0) {
        console.log('Uh... I guess I need to handle this. But for now, it will break me.');
        return;
      }

      // Send the query for the first pattern to start the fun
      this.batTreeCharacteristic.writeValueWithoutResponse(new TextEncoder().encode('GetPat 0\n'));
      this.currentPattern = 0;
      this.BatState = 'gatherPatterns';
    }

    else if (this.BatState === 'gatherPatterns') {

      console.log('Pattern ' + this.currentPattern + ': ' + respStr);
      let localPatterns = this.state.patterns;

      localPatterns.push({
        name: respStr,
        active: false
      })
      this.currentPattern++;

      this.setState(() => ({patterns: localPatterns}));

      if (this.numPatterns > this.currentPattern) {
        let queryString = 'GetPat ' + this.currentPattern + '\n';
        this.batTreeCharacteristic.writeValueWithoutResponse(new TextEncoder().encode(queryString));
      }
      else {
        console.log('I have received all of my patterns?');
        console.log(localPatterns);
        this.BatState = 'queryNumAni';
        this.batTreeCharacteristic.writeValueWithoutResponse(new TextEncoder().encode('NumAni\n'));
      }
    }

    else if (this.BatState === 'queryNumAni') {
      this.numAnimations = parseInt(respStr);

      if (this.numAnimations === 0) {
        console.log('Same blah blah broken blah blah');
        return;
      }

      // Send the query for the first animation to start the fun
      this.batTreeCharacteristic.writeValueWithoutResponse(new TextEncoder().encode('GetAni 0\n'));
      this.BatState = 'gatherAnimations';
    }

    else if (this.BatState === 'gatherAnimations') {
      console.log('Animation ' + this.currentAnimation + ': ' + respStr);
      let localAnimations = this.state.animations;
      localAnimations.push({
        name: respStr,
        active: false
      })

      this.currentAnimation++;

      this.setState(() => ({animations: localAnimations}));

      if (this.numAnimations > this.currentAnimation) {
        let queryString = 'GetAni ' + this.currentAnimation + '\n';
        this.batTreeCharacteristic.writeValueWithoutResponse(new TextEncoder().encode(queryString));
      }
      else {
        console.log('I have received all of my animations?');
        console.log(localAnimations);

        this.batTreeCharacteristic.writeValueWithoutResponse(new TextEncoder().encode('GetBright\n'));
        this.BatState = 'queryBright';
      }
    }

    else if (this.BatState === 'queryBright') {
      console.log(respStr);
      const brightness = (parseInt(respStr)+1)/5*100;
      this.setState(() => ({ brightness }));

      this.BatState = 'queryVol';
      this.batTreeCharacteristic.writeValueWithoutResponse(new TextEncoder().encode('GetVol\n'));
    }

    else if (this.BatState === 'queryVol') {
      const volume = (parseInt(respStr)+1)/8*100;
      this.setState(() => ({ volume }));
      
      this.BatState = 'readyToRock';
    }

    else {
      console.log(`Response: ${respStr}`);
    }

  }

  setPattern = (patNum) => {
    this.batTreeCharacteristic.writeValueWithoutResponse(new TextEncoder().encode(`SetPat ${patNum}\n`));

    // First, clear all selected icons
    let patterns = this.state.patterns;
    let animations = this.state.animations;
    patterns.forEach((pattern) => { pattern.active = false; })
    animations.forEach((animation) => {animation.active = false; })

    // Enable the selected icon
    patterns[patNum].active = true;
    
    this.setState(() => ({ patterns, animations }));
    this.lastPlayedPattern = patNum;
  }

  setAnimation = (aniNum) => {
    this.batTreeCharacteristic.writeValueWithoutResponse(new TextEncoder().encode(`SetAni ${aniNum}\n`));

    // First, clear all selected icons
    let patterns = this.state.patterns;
    let animations = this.state.animations;
    patterns.forEach((pattern) => { pattern.active = false; })
    animations.forEach((animation) => {animation.active = false; })

    // Enable the selected icon
    animations[aniNum].active = true;
    
    this.setState(() => ({ patterns, animations }));
  }

  handleIncBright = () => {
    console.log('I got called...');
    this.BatState = 'queryBright';
    this.batTreeCharacteristic.writeValueWithoutResponse(new TextEncoder().encode(`IncBright\n`));
  }

  handleDecBright = () => {
    this.BatState = 'queryBright';
    this.batTreeCharacteristic.writeValueWithoutResponse(new TextEncoder().encode(`DecBright\n`));
  }

  handleIncVol = () => {
    this.BatState = 'queryVol';
    this.batTreeCharacteristic.writeValueWithoutResponse(new TextEncoder().encode(`IncVol\n`));
  }

  handleDecVol = () => {
    this.BatState = 'queryVol';
    this.batTreeCharacteristic.writeValueWithoutResponse(new TextEncoder().encode(`DecVol\n`));
  }



  render() {
    return (
    <Box sx={{flexGrow: 1, pb: '20px'}}>
      <CssBaseline />
      <Header openSettings={this.handleOpenSettings} /> 
      <Container maxWidth='sm' sx={{display: 'flex', flexDirection:'column', alignItems: 'center', justifyContent:'vertical'}}>
          {!this.state.connected && 
            <Box 
            sx={{
              display: 'flex',
              width: '100%',
              alignItems: 'center',
              justifyContent: 'center',
              bgcolor: 'background.default',
              color: 'text.primary',
              borderRadius: 1,
              p: 3,
            }}>
            <Button onClick={this.onClickConnect}>Connect</Button>  
          </Box>
          }
        <EffectsList name='Patterns' effects={this.state.patterns} setEffect={this.setPattern} icon={<EmojiObjectsIcon />} />
        <EffectsList name='Animations' effects={this.state.animations} setEffect={this.setAnimation} icon={<MusicNoteIcon />} />
      </Container>

      <SettingsDialog 
        open={this.state.settingsOpen} 
        onClose={this.handleCloseSettings} 
        brightPct={this.state.brightness} 
        volPct={this.state.volume} 
        incBrightness={this.handleIncBright}
        decBrightness={this.handleDecBright}
        incVolume={this.handleIncVol}
        decVolume={this.handleDecVol}
      />

    </Box>
  )};
}

export default Controller;